/*Variables*/
:root {
  --white: #fff;
  --black: #000;
  --eggshell: #eee8d6;
  --darkblue: #022933;
  --yellow: #f3d350;
  --blue: #0076a3;
  --green: #bfc406;
  --orange: #dc762b;
  --red: #d14348;
  --purple: #6d73c2;
  --color-light: var(--white);
  --color-dark: var(--black);
  --color-main: var(--color-light);
  --color-background: var(--blue);
  --color-links: var(--yellow);
  --color-navigation-links: var(--blue);
  --font-size-base: 1.3rem;
  --line-height-base: 150%;
  --font-primary: 'Work Sans', -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  --font-weight-light: 100;
  --font-weight-normal: 300;
  --font-weight-medium: 500;
  --font-weight-bold: 700;
  /*custom properties*/
  --container-small-width: 50%;
  --container-default-width: 90%;
  --container-max-width: 1200px;
  --width-standard-width: var(--container-default-width);
  --width-standard-max-width: var(--container-max-width);
  --width-standard-margin: 0 auto;
  /*Custom media*/
  @custom-media --viewport-small (width >= 576px);
  @custom-media --viewport-medium (width >= 768px);
  @custom-media --viewport-large (width >= 992px);
  @custom-media --viewport-extralarge (width >= 1200px);
  @custom-selector :--headings h1, h2, h3, h4, h5, h6;
  /* Material Design Adaptive Breakpoints */
  /*
    Below you'll find CSS media queries based on the breakpoint guidance
    published by the Material Design team. You can choose to use, customise
    or remove these breakpoints based on your needs.

    http://www.google.com/design/spec/layout/adaptive-ui.html#adaptive-ui-breakpoints
   */
  /* mobile-small */
  /* mobile-large */
  /* mobile-small-landscape */
  /* mobile-large-landscape */
  /* tablet-small-landscape */
  /* tablet-large-landscape */
  /* tablet-small */
  /* tablet-large */
  /* desktop-x-small-landscape */
  /* desktop-x-small */
  /* desktop-small-landscape */
  /* desktop-small */
  /* desktop-medium-landscape */
  /* desktop-medium */
  /* desktop-large */
  /* desktop-xlarge */ }

